import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function evToSalesTtm(tableName: string) {
  const title = "EV/Sales (LTM)"
  return genericColumn({
    endpoint: "companyKeyMetrics",
    columnKeys: ["evToSalesTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "EV vs. revenue.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyKeyMetrics}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about enterprise value to sales or other relevant financial measures?`,
              value: info.row.original.financialData.companyKeyMetrics?.data?.evToSalesTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyKeyMetrics?.data?.evToSalesTtm}
        />
      )
    },
  })
}

export { evToSalesTtm }
